@import '~@papillonads/css/build/primer/support/variables/layout.scss';
@import '~@papillonads/css/build/primer/support/variables/colors.scss';
@import '~@papillonads/css/build/primer/base';
@import '../index.scss';

.breadcrumb-item {
  display: inline-block;
  /* stylelint-disable-next-line primer/spacing*/
  margin-left: -0.35em;
  white-space: nowrap;
  list-style: none;

  &::before {
    padding-right: $em-spacer-2;
    padding-left: $em-spacer-2;
    content: ' ';
  }

  &::after {
    padding-right: $em-spacer-5;
    padding-left: $em-spacer-5;
    /* stylelint-disable-next-line primer/colors*/
    color: $border-gray;
    content: '>';
  }

  &:first-child {
    margin-left: 0;
  }
}

.breadcrumb-item-selected,
.breadcrumb-item[aria-current]:not([aria-current='false']) {
  color: $text-gray;

  &::before {
    padding-right: $em-spacer-2;
    padding-left: $em-spacer-2;
    content: ' ';
  }

  &::after {
    padding-right: $em-spacer-5;
    padding-left: $em-spacer-5;
    content: '>';
  }
}

/* custom-style*/
.breadcrumb-inactive {
  @include inactive;

  li,
  a {
    color: $gray-400 !important;
  }
}
