@import '~@papillonads/css/build/primer/support/variables/typography.scss';
@import '~@papillonads/css/build/primer/support/variables/colors.scss';
@import '~@papillonads/css/build/primer/support/variables/misc.scss';

/* A nice way to display branch names inside the UI. Can be a link or not.*/
/* stylelint-disable selector-max-type*/
.branch-name {
  display: inline-block;
  /* stylelint-disable-next-line primer/spacing*/
  padding: 2px 6px;
  font: 12px $mono-font;
  /* stylelint-disable-next-line primer/colors*/
  color: rgba($black, 0.6);
  /* stylelint-disable-next-line primer/colors*/
  background-color: lighten($blue-100, 3%);
  border-radius: $border-radius;

  .octicon {
    /* stylelint-disable-next-line primer/spacing*/
    margin: 1px -2px 0 0;
    /* stylelint-disable-next-line primer/colors*/
    color: desaturate($blue-300, 70%);
  }
}

/* When a branch name is a link*/
/* stylelint-disable selector-no-qualifying-type*/
a.branch-name {
  color: $text-blue;
}
/* stylelint-enable selector-no-qualifying-type*/
