/* stylelint-disable block-closing-brace-space-before, comment-empty-line-before*/

/* Type scale variables found in ../support/lib/variables.scss*/
/* $h00-size-mobile: 40px;*/
/* $h0-size-mobile: 32px;*/
/* $h1-size-mobile: 26px;*/
/* $h2-size-mobile: 22px;*/
/* $h3-size-mobile: 18px;*/
/* $h00-size: 48px;*/
/* $h0-size: 40px;*/
/* $h1-size: 32px;*/
